.App {
  text-align: center;
}

.App-logo {
  height: 7rem;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.badge-image {
  height: 30px;
  border: 0.5px white solid;
  border-radius: 5px;
}

.center-button {
  display: flex;
  margin: 0 auto;
}

.footer{
  background-color:rgba(51,51,51,0.08);
  margin-bottom: 0;
  align-items: center;
  color: white;
  padding: 5px 0;
}

.footer-item {
  margin: 0.5rem;
}

.footer-item a {
  margin: 0.5rem;
  color: white;
}

.input-field i {
  color: white;
}

.input-field .helper-text {
    color: white;
}

.main-container {
  min-height: calc(100vh - 61px);
  min-width: 350px;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.modal-buttons {
  padding: 20px 0;
}

.modal-content h4 {
  padding: 20px 0;
}

.pad {
  margin: 20px;
}

[type="checkbox"] + span:not(.lever) { 
  color: white;
  line-height: 21px;
}

.modal-cancel {
  float: left;
}

.modal-submit {
  float: right;
}

.instructions {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.instructions p {
  font-size: 1rem;
  color: white;
  text-align: justify;
}

.instructions-item {
  flex: 1 1 100%;
}

.pay-button {
  justify-content: center;
  text-decoration: none;
  color: #fff;
  background-color: #26a69a;
  text-align: center;
  letter-spacing: 0.5px;
  transition: 0.2s ease-out;
  cursor: pointer;
  height: 3rem;
  line-height: 3rem;
}

.progress {
  width: 90%;
}

.progress-background {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(238, 238, 238, 0.75);
  
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;  
}

.promo-input {
  margin: 0;
}

.promo-row {
  align-items: center;
}

@media only screen and (max-width: 600px) {
  .row.promo-row {
    justify-content: left;
  }

  .promo-text-col {
    margin-top: 15px;
  }
}

.promo-row .input-field{
  margin: 0;
  height: 50px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%
}

.StripeCheckout span {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-image: none !important;
  justify-content: center;
  text-decoration: none;
  color: #fff;
  background-color: #26a69a;
  text-align: center;
  letter-spacing: 0.5px;
  transition: 0.2s ease-out;
  cursor: pointer;
  height: 3rem !important;
  line-height: 3rem !important;
  padding: 0px 12px;
  border-radius: 0 !important;
  font-weight: normal !important;
  box-shadow: none !important;
  text-shadow: none !important;
}
.StripeCheckout {
  background-image: none !important;
  justify-content: center;
  text-decoration: none;
  color: #fff;
  background-color: #26a69a;
  text-align: center;
  letter-spacing: 0.5px;
  transition: 0.2s ease-out;
  cursor: pointer;
  height: 3rem;
  line-height: 3rem;
  padding: 0;
  border-radius: 2px !important;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
    0 3px 1px -2px rgba(0, 0, 0, 0.2) !important;
  margin: 0 0 1rem;
}

.subtext {
  color: white;
  font-size: 18px;
}

.toast {
  font-weight: 500;
}

.toast i {
  color: white;
}

.toastError {
  background-color: red;
}

.toastWarning {
  background-color: orange;
}

.toastSuccess {
  background-color: green;
}

.toastMessage {
  background-color: teal;
}

